import './autopilot-loader.css'
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';
import Select from '@mui/material/Select';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import Dialog from '@mui/material/Dialog';
import { DialogTitle } from '@mui/material';
import Button from '@mui/material/Button';
import PsychologyIcon from '@mui/icons-material/Psychology';
import MenuItem from '@mui/material/MenuItem';
import axios from 'axios';
import { useEffect, useState } from 'react';

export default function AutopilotLoader(){
    const [models, setModels] = useState([]);
    const [games, setGames] = useState([]);
    const [uniqueTeams, setUniqueTeams] = useState([]);
    const [teamOfInterest, setTeamOfInterest] = useState("");
    const [errorMessage, setErrorMessage] = useState(""); 
    const [selectedGame, setSelectedGame] = useState("");
    const [selectedModel, setSelectedModel] = useState("");
    const [isModelLinkShown, setIsModelLinkShown] = useState(false);

    const loadGame = () => {
        if(selectedGame.length == 0){
            setErrorMessage("A game must be selected to load.");
            setIsModelLinkShown(false);
            return;
        } else if(selectedModel.length == 0){
            setErrorMessage("A model must be selected to load.");
            setIsModelLinkShown(true);
            return;
        }
        setIsModelLinkShown(false);
        window.location.href = `/endzone/ingame/autopilot?gameId=${selectedGame}&modelId=${selectedModel}`
    }   

    const getUniqueTeams = (teams) => {
        let uniqueTeamsSet = new Set();
        teams.forEach((game) => {
            uniqueTeamsSet.add(game.Home_Team);
            uniqueTeamsSet.add(game.Away_Team)
        })
        console.log(uniqueTeamsSet)
        setUniqueTeams([...uniqueTeamsSet]);
    }

    const gameSelectChangeHandler = (e) => {
        setSelectedGame(e.target.value);
    }

    const modelSelectChangeHandler = (e) => {
        setSelectedModel(e.target.value);
    }

    const teamOfInterestHandler = (e) => {
        setTeamOfInterest(e.target.value)
    }

    const getModels = () => {
        axios.get('/endzone/models/get').then((response) => {
            setModels(response.data);
        }, (error) => {
            setModels([]);
            console.error(error);
            setErrorMessage("Unable to fetch Models, please refresh page.")
        })
    }

    const getGames = () => {
        axios.get('/endzone/data/game/get').then((response) => {
            setGames(response.data);
            getUniqueTeams(response.data);
        }, (error) => {
            setGames([]);
            console.error(error);
            setErrorMessage("Unable to fetch Games, please refresh page.")
        })
    }


    // Triggers on Component Load
    useEffect(() => {
        getModels();
        getGames();
    }, [])

    return (
        <Dialog open={true} sx={{backgroundColor: '#0d1019'}}>
        <DialogTitle sx={{backgroundColor: "white", paddingBottom: '0px'}}>
            <h3 style={{fontFamily: 'Audiowide', fontSize: '15px', color: 'black', textDecoration: 'underline'}}>Endzone Autopilot System</h3>
        </DialogTitle>
        <DialogContent sx={{backgroundColor: "white", display: 'inline-block'}}>
            <FormControl fullWidth sx={{marginTop: '5px'}}>
                <InputLabel id='game-select-label' sx={{fontFamily: 'audiowide'}}>Team of Interest</InputLabel>
                <Select
                    labelId="game-select-label"
                    label="Game"
                    onChange={teamOfInterestHandler}
                >
                    {
                        uniqueTeams.map((uniqueTeam) => {
                            return (<MenuItem value={uniqueTeam}>{uniqueTeam}</MenuItem>)
                        })
                    }
                </Select>
            </FormControl>
            <FormControl fullWidth sx={{marginTop: '10px'}}>
                <InputLabel id='game-select-label' sx={{fontFamily: 'audiowide'}}>Game</InputLabel>
                <Select
                    labelId="game-select-label"
                    label="Game"
                    onChange={gameSelectChangeHandler}
                >   
                    {
                        teamOfInterest.length > 0 ?
                            games.map((game) => {
                                if(teamOfInterest == game.Home_Team || teamOfInterest == game.Away_Team)
                                {
                                    return (<MenuItem value={game.Game_ID}>{game.Away_Team + ' at ' + game.Home_Team}</MenuItem>)
                                }
                            })
                        :
                        games.map((game) => {
                            return (<MenuItem value={game.Game_ID}>{game.Away_Team + ' at ' + game.Home_Team}</MenuItem>)
                        })
                    }
                </Select>
            </FormControl>
            <FormControl fullWidth sx={{marginTop: '10px'}}>
                <InputLabel id='model-select-label' sx={{fontFamily: 'audiowide'}}>Model</InputLabel>
                <Select
                    labelId="model-select-label"
                    label="Model"
                    onChange={modelSelectChangeHandler}
                >
                    {
                        models.map((model) => {
                            return(<MenuItem value={model.id}>{model.ModelName}</MenuItem>)
                        })
                    }
                </Select>
            </FormControl>
            <p style={{fontFamily: 'Audiowide', color: '#0984e3'}}>{errorMessage}</p>
            {
                isModelLinkShown ? 
                <a style={{fontFamily: 'audiowide', fontSize: '15px', color: '#0984e3'}} href='/endzone/pregame/models'>No prediction models? Click to create a new one.</a>
                :
                null
            }
        </DialogContent>
        <DialogActions sx={{backgroundColor: "white"}}>
            <Button variant="contained" endIcon={<PsychologyIcon />} sx={{backgroundColor: '#17181d', fontFamily: 'Audiowide', color: 'white'}} onClick={() => {loadGame()}}>
                Load Game
            </Button>
        </DialogActions>
    </Dialog>
    )
}